@import url("https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700,800,900&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins" sans-serif;
}
body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: #091921;
}
.calculator {
  position: relative;
  display: grid;
}
.calculator .value {
  grid-column: span 7;
  height: 100px;
  text-align: right;
  border: none;
  outline: none;
  padding: 10px;
  font-size: 18px;
}
.calculator span {
  display: grid;
  width: 60px;
  height: 60px;
  color: #fff;
  background: #0c2835;
  place-items: center;
  border: 1px solid rgba(0, 0, 0, 0.25);
}

.calculator span.clear {
  background: #ff3077;
}
.calculator span.rem {
  background: orange;
}

.calculator span.equal {
  background: #03b1ff;
}

.calculator span:active {
  background: #74ff3b;
  color: #111;
}
